* {
    font-family: "Lato", sans-serif;
    
}

.start-body {
  background-color: #effae9;
  display: flex;
  flex-direction: row;
  margin: 0;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  min-height: 100vh;
  height: 100%;
  overflow: hidden;
  padding: 10px;
  padding-left: 20%;
  padding-right: 20%;
}

.start-body h1 {
  font-weight: 900;
  font-size: 3em;
  margin-bottom: 0;
  font-family:'Merriweather', serif;
}

.start-body h3 {
  font-size: 1.5em;
  font-weight: normal;
  font-family:'lato', sans-serif;
}

.play-button {
  padding: 15px;
  width: 200px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 30px;
  font-size: 17px;
  margin-bottom: 40px;
  margin: 0.5rem;
}

.how-to-button {
  padding: 15px;
  width: 200px;
  background-color: transparent;
  color: black;
  border: solid 1px black;
  border-radius: 30px;
  font-size: 17px;
  margin-bottom: 40px;
  margin: 0.5rem;
}

.button-section {
  display: flex;
  width: 100%;
  justify-content: center;
}

button:hover {
  cursor: pointer;
}

.yellowdot {
    min-height: 25px;
    min-width: 25px;
    max-height: 25px;
    max-width: 25px;
    border: solid black;
    border-width: 2px;
    background-color: yellow;
    border-radius: 50%;
    display: inline-block;
    margin-left: 3px;
    margin-top: 10px;
  }

  .greydot {
    min-height: 25px;
    min-width: 25px;
    max-height: 25px;
    max-width: 25px;
    border: solid black;
    border-width: 2px;
    background-color: gray;
    border-radius: 50%;
    display: inline-block;
    margin-left: 3px;
    margin-top: 10px;
  }

  .greendot {
    min-height: 25px;
    min-width: 25px;
    max-height: 25px;
    max-width: 25px;
    border: solid black;
    border-width: 2px;
    background-color: green;
    border-radius: 50%;
    display: inline-block;
    margin-left: 3px;
    margin-top: 10px;
  }

  .help-category {
    display: flex;
    column-gap: 10px;
  }